

























export default {
	data() {
		return {
			news: [],
			topNews: []
		};
	},
	onLoad:function() {
		uni.request({
			url: 'http://news-at.zhihu.com/api/4/news/latest',
			method: 'GET',
			data: {},
			success: res => {
				this.news = res.data.stories;
				this.topNews = res.data.top_stories;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		openInfo(e) {
			var id = e.currentTarget.dataset.newsid;
			uni.navigateTo({
				url: '../info/info?newsid='+id
			});
		}
	},
}



